@keyframes portfolio-in-right {
  0% {
    left: 100%;
    opacity: 0;
    transform: perspective(100px) rotateY(-5deg); }
  100% {
    left: 0;
    opacity: 1;
    transform: perspective(100px) rotateY(0deg); } }

@keyframes portfolio-out-right {
  0% {
    left: 0;
    opacity: 1;
    transform: perspective(100px) rotateY(0deg); }
  100% {
    left: -100%;
    opacity: 0;
    transform: perspective(100px) rotateY(5deg); } }

@keyframes portfolio-in-left {
  0% {
    right: 100%;
    opacity: 0;
    transform: perspective(100px) rotateY(5deg); }
  100% {
    right: 0;
    opacity: 1;
    transform: perspective(100px) rotateY(0deg); } }

@keyframes portfolio-out-left {
  0% {
    right: 0;
    opacity: 1;
    transform: perspective(100px) rotateY(0deg); }
  100% {
    right: -100%;
    opacity: 0;
    transform: perspective(100px) rotateY(-5deg); } }

@keyframes service-icon-in {
  0% {
    opacity: 0;
    transform: scale(0.1); }
  20% {
    transform: scale(1.2); }
  40% {
    transform: scale(0.8); }
  60% {
    transform: scale(1.1); }
  80% {
    transform: scale(0.9); }
  100% {
    opacity: 0.5;
    transform: scale(1); } }

@keyframes service-icon-in-dark {
  0% {
    opacity: 0;
    transform: scale(0.1); }
  20% {
    transform: scale(1.2); }
  40% {
    transform: scale(0.8); }
  60% {
    transform: scale(1.1); }
  80% {
    transform: scale(0.9); }
  100% {
    opacity: 1;
    transform: scale(1); } }

