/*
Styles the testimonial widget.

@since 1.0.0
*/
/*
Sets up the theme's styling properties and makes some variables already set in Foundation available for easy use
throughout this theme's SASS files.

@since 0.1.0
*/
@keyframes testimonial_transition_in {
  0% {
    transform: perspective(100px) rotateY(-10deg); }
  100% {
    transform: perspective(100px) rotateY(0deg); } }

@keyframes testimonial_transition_out {
  0% {
    transform: perspective(100px) rotateY(0deg); }
  100% {
    transform: perspective(100px) rotateY(10deg); } }

.testimonials-container {
  position: relative;
  overflow: hidden; }

.testimonials-container .testimonials-prev, .testimonials-container .testimonials-next {
  color: #ccc;
  position: absolute;
  top: 60px;
  font-size: 20px;
  transition: color 300ms;
  cursor: pointer;
  z-index: 10; }

.testimonials-container .testimonials-prev:hover, .testimonials-container .testimonials-next:hover {
  color: #555; }

.testimonials-container .testimonials-prev {
  left: 0; }

.testimonials-container .testimonials-next {
  right: 0; }

.testimonials-container .testimonials {
  list-style: none;
  margin: 0;
  position: relative; }

.testimonials-container .testimonials.js {
  transition: left 300ms; }

.testimonials-container .testimonials.js .testimonial {
  float: left;
  opacity: 0;
  transition: opacity 300ms;
  animation: testimonial_transition_out 500ms; }

.testimonials-container .testimonials.js .testimonial.active {
  opacity: 1;
  animation: testimonial_transition_in 500ms; }

.testimonials-container .testimonial {
  color: #777; }

.testimonials-container .testimonial .testimonial-image {
  text-align: center;
  margin: 20px 0;
  position: relative; }

.testimonials-container .testimonial .testimonial-image img {
  overflow: hidden;
  border: 10px solid #ccc;
  border-radius: 50%;
  width: 100px;
  height: 100px; }

.testimonials-container .testimonial .testimonial-image:after {
  content: '';
  border-top: 16px solid #ccc;
  border-left: 16px solid transparent;
  border-right: 16px solid transparent;
  position: absolute;
  left: 50%;
  bottom: -10px;
  margin-left: -16px; }

.testimonials-container .testimonial .testimonial-author {
  text-align: center;
  font-family: "Helvetica Neue", Helvetica, Roboto, Arial, sans-serif;
  text-transform: uppercase;
  font-size: 1.3em; }

.testimonials-container .testimonial .testimonial-author .testimonial-role {
  color: #12538F; }

.testimonials-container .testimonial .testimonial-content .icon-quote {
  font-size: 35px;
  color: #ddd; }

